import React, { Fragment, useEffect, useState } from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import MoveForward from "../components/common/move-forward";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import PortfolioOne from "../screens/portfolio/portfolio-page";
import { utils } from "../core/helper";

let metaInfo;
const PortfolioPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Portfolio", link: null },
  ];
  const [isMounted, setIsMounted] = useState(false);
  console.log("fun called:");

  useEffect(() => {
    async function fetchMetaInfo(db) {
      try {
        console.error("fun called:");

        const resp = await utils.getDocDataFilter(
          db,
          "meta",
          "slug",
          "portfolio"
        );
        if (resp) {
          metaInfo = resp?.[0];
        }
      } catch (err) {
        console.error("Error fetching data:", err);
      }
    }
    if (isMounted) {
      fetchMetaInfo(db);
    }
  }, [isMounted, db]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  return (
    <Fragment>
      <Layout>
        <Seo
          title={metaInfo?.title}
          metaTitle={metaInfo?.metaTitle}
          description={metaInfo?.description}
          ogTitle={metaInfo?.ogTitle}
          ogType={metaInfo?.ogType}
          ogDescription={metaInfo?.ogDescription}
          twitterTitle={metaInfo?.twitterTitle}
          twitterDescription={metaInfo?.twitterDescription}
          // image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
        ></Seo>
        <BreadCrumb items={breadcrumbLinks} />
        <div className="mt-5">
          <PortfolioOne />
          <MoveForward db={db} />
        </div>
      </Layout>
    </Fragment>
  );
};

export const Head = (props) => {
  const { data } = props;
  // const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  // const db = getFirestore(app);
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  console.log("metaInfo::", metaInfo);

  return (
    <Fragment>
      <Seo
        title={metaInfo?.title}
        metaTitle={metaInfo?.metaTitle}
        description={metaInfo?.description}
        ogTitle={metaInfo?.ogTitle}
        ogType={metaInfo?.ogType}
        ogDescription={metaInfo?.ogDescription}
        twitterTitle={metaInfo?.twitterTitle}
        twitterDescription={metaInfo?.twitterDescription}
        image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
      ></Seo>
    </Fragment>
  );
};

export default PortfolioPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
